import Vue from 'vue'
import App from './App.vue'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faHome, faTimes, faEnvelope, faMobile, faMapMarkerAlt, faBars } from '@fortawesome/free-solid-svg-icons'
import { faLine, faFacebook, faLinkedin  } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon, FontAwesomeLayers, FontAwesomeLayersText } from '@fortawesome/vue-fontawesome'
import '@/assets/css/tailwind.css'

library.add(faHome, faTimes, faBars, faEnvelope, faMobile, faMapMarkerAlt, faLine, faFacebook, faLinkedin)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.component('font-awesome-layers-text', FontAwesomeLayersText)

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
